.App {
  /* text-align: center; */
}

button {
  cursor: pointer;
}

.header {
  padding: 20px;
  background-color: #000;
  color: #fff;
  text-align: left;
}

.header-title {
  margin: 0;
}

.list {
  padding: 20px;
  border-bottom: 1px solid #000;
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 10;
  margin: 0 auto;
  padding: 20px;
  padding-top: 0;
  border-radius: 8px;
  background-color: #eee;
  transform: translate(-50%, -50%);
}

.list-header-title {
  display: inline-block;
  cursor: pointer;
}

.list-button-container button {
  background-color: transparent;
  border: none;
}

.sort-btn-container {
  display: flex;
  justify-content: center;
  gap: 12px;
}

button {
  background: #000;
  color: #fff;
  border: 0;
}

.sort-btn {
  margin: 20px 0;
  padding: 16px 24px;
  border-radius: 8px;
}

.input-container {
  display: flex;
  justify-content: center;
  gap: 12px;
  margin-bottom: 16px;
}

.input-container button,
.delete-btn,
.change-btn {
  padding: 8px 12px;
  border-radius: 4px;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #000;
  opacity: 0.4;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
